enter;\n    position: relative;\n    text-decoration: none;\n    overflow-wrap: break-word;\n    line-height: 1.7;\n    cursor: pointer;\n    border: none;\n    width: 100%;\n    background: none;\n    text-align: left;\n    @include breakpoints.breakpoint(phablet) {\n        padding: 15px 30px;\n    }\n    &:focus-visible {\n        outline-color: colors.$primary-text;\n        outline-style: auto;\n        outline-width: 2px;\n        outline-offset: 0px;\n    }\n    .panelText {\n        font-family: typography.$header-font;\n        font-weight: bold;\n        font-size: 18px;\n        flex: 1;\n    }\n    .panelChevron {\n        box-sizing: border-box;\n        position: relative;\n        display: block;\n        width: 18px;\n        height: 18px;\n        border-radius: 100px;\n        margin-right: 6px;\n        @include breakpoints.breakpoint(phablet) {\n            margin-right: 0;\n        }\n        &::after {\n            content: \"\";\n            display: block;\n            box-sizing: border-box;\n            position: absolute;\n            width: 18px;\n            height: 18px;\n            border-bottom: 2px solid;\n            border-right: 2px solid;\n            transform: translateY(-30%) rotate(45deg);\n            transition: transform 0.3s ease-in-out;\n        }\n        &.expanded {\n            &::after {\n                transform: translateY(30%) rotate(225deg);\n            }\n        }\n    }\n}\n.content {\n    opacity: 0;\n    margin-top: 0px;\n    line-height: 0;\n    max-height: 0;\n    overflow: hidden;\n    padding: 0 15px 0;\n    @include breakpoints.breakpoint(phablet) {\n        padding: 0 30px 0;\n    }\n    &.initialized {\n        animation: hide 0.3s ease-in-out;\n    }\n\n    &.expanded {\n        animation: show 0.3s ease-in-out;\n        opacity: 1;\n        margin-top: 0px;\n        padding: 8px 15px 15px;\n        line-height: inherit;\n        max-height: 10000px;\n        @include breakpoints.breakpoint(phablet) {\n            padding: 8px 30px 15px;\n        }\n    }\n}\n","@use \"../variables/breakpoints\";\n\n@mixin breakpoint($point) {\n    @if $point == desktop {\n        @media (min-width: breakpoints.$screen-desktop) {\n            @content;\n        }\n    } @else if $point == laptop {\n        @media (min-width: breakpoints.$screen-laptop) {\n            @content;\n        }\n    } @else if $point == tablet {\n        @media (min-width: breakpoints.$screen-tablet) {\n            @content;\n        }\n    } @else if $point == phablet {\n        @media (min-width: breakpoints.$screen-phablet) {\n            @content;\n        }\n    }\n}\n","$body-background: #ffffff;\n$navigation-bar-background:  #f7f7f7;\n$hover-background: rgba(0,0,0,.05);\n$default-border: #d8d8d8;\n\n$default-background: #eee;\n$default-background-hover: #ddd;\n$default-text:#2F3940;\n\n$light-gray: #f6f5f4;\n\n$primary-brand: #FE5000;\n\n$primary-background: #3767C7;\n$primary-background-hover: #FE5000;\n$primary-text: #3767C7;\n$primary-text-hover:#0056b3;\n\n\n$success-background: #06A755;\n$success-background-hover: #009745;\n$success-text: #06A755;\n\n\n$warning-background: #C38621;\n$warning-background-hover: #B37611;\n$warning-text: #C38621;\n\n$danger-background: #E00d0D;\n$danger-background-hover: #D00000;\n$danger-text: #E00d0D;\n\n$disabled-background: #f4f4f4;\n$disabled-border: #dedede;\n$disabled-text: #707070;\n\n\n$light-green: #8fff33;\n$light-yellow: #ffEb3b;\n$light-red: #ff4a2e;\n","﻿@mixin appearance ($value) {\n    -webkit-appearance: $value;\n    -moz-appearance: $value;\n    appearance: $value;\n}\n","@mixin border-radius($radius) {\n  -webkit-border-radius: $radius;\n  border-radius: $radius;\n}\n\n// Single side border-radius\n\n@mixin border-top-radius($radius) {\n  -webkit-border-top-right-radius: $radius;\n  border-top-right-radius: $radius;\n  -webkit-border-top-left-radius: $radius;\n  border-top-left-radius: $radius;\n}\n@mixin border-right-radius($radius) {\n